"use client";
import { useEffect, useState, useRef } from "react";
import { auth, db, storage } from "../firebase";
import { collection, query, where, onSnapshot, orderBy, addDoc, doc, setDoc, getDocs } from "firebase/firestore";
import { signOut, updateProfile } from "firebase/auth";
import { ref, uploadBytes, getDownloadURL } from "firebase/storage";
import styles from "../styles/chat.module.css";

export default function ChatApp() {
  const [users, setUsers] = useState([]);
  const [selectedUser, setSelectedUser] = useState(null);
  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState("");
  const [currentScreen, setCurrentScreen] = useState("chat"); // chat, profile, config, voice, addContact
  const [theme, setTheme] = useState("light");
  const [font, setFont] = useState("Arial");
  const [nametagInput, setNametagInput] = useState("");
  const [contactEmail, setContactEmail] = useState("");

  const user = auth.currentUser;
  const messagesEndRef = useRef(null);

  // Redireciona para login se não estiver logado
  useEffect(() => {
    if (!user) window.location.href = "/login";
  }, [user]);

  // Carrega usuários
  useEffect(() => {
    if (!user) return;
    const q = query(collection(db, "users"), where("uid", "!=", user.uid));
    const unsub = onSnapshot(q, snapshot => {
      setUsers(snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() })));
    });
    return unsub;
  }, [user]);

  // Carrega mensagens
  useEffect(() => {
    if (!selectedUser || !user) return;
    const messagesRef = collection(db, "messages");
    const q = query(messagesRef, orderBy("timestamp", "asc"));

    const unsub = onSnapshot(q, snapshot => {
      const msgs = snapshot.docs
        .map(doc => doc.data())
        .filter(msg =>
          (msg.senderId === user.uid && msg.receiverId === selectedUser.uid) ||
          (msg.senderId === selectedUser.uid && msg.receiverId === user.uid)
        );
      setMessages(msgs);
      scrollToBottom();
    });
    return unsub;
  }, [selectedUser, user]);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  const handleSendMessage = async () => {
    if (!newMessage.trim() || !selectedUser) return;
    await addDoc(collection(db, "messages"), {
      senderId: user.uid,
      receiverId: selectedUser.uid,
      text: newMessage,
      timestamp: new Date()
    });
    setNewMessage("");
  };

  const handleLogout = () => {
    signOut(auth).then(() => window.location.href = "/login");
  };

  const handleThemeChange = () => {
    setTheme(theme === "light" ? "dark" : "light");
  };

  const handleFontChange = (e) => {
    setFont(e.target.value);
  };

  const handleProfileUpdate = async (newNametag, newPhoto) => {
    let finalNametag = newNametag;
    if (newNametag !== user.nametag) {
      finalNametag = await generateUniqueNametag(newNametag);
    }

    let photoURL = user.photoURL;
    if (newPhoto) {
      const photoRef = ref(storage, `profilePhotos/${user.uid}`);
      await uploadBytes(photoRef, newPhoto);
      photoURL = await getDownloadURL(photoRef);
    }

    await updateProfile(user, { displayName: user.displayName, photoURL });
    await setDoc(doc(db, "users", user.uid), {
      ...user,
      nametag: finalNametag,
      photoURL
    });
    alert("Perfil atualizado!");
  };

  const generateUniqueNametag = async (base) => {
    const usersRef = collection(db, "users");
    let tag = base;
    let counter = 1;
    while (true) {
      const q = query(usersRef, where("nametag", "==", tag));
      const snapshot = await getDocs(q);
      if (snapshot.empty) break;
      tag = `${base}#${counter}`;
      counter++;
    }
    return tag;
  };

  const handleAddContact = async () => {
    let foundUser = null;
    if (contactEmail) {
      const q = query(collection(db, "users"), where("email", "==", contactEmail));
      const snapshot = await getDocs(q);
      if (!snapshot.empty) foundUser = snapshot.docs[0].data();
    } else if (nametagInput) {
      const q = query(collection(db, "users"), where("nametag", "==", nametagInput));
      const snapshot = await getDocs(q);
      if (!snapshot.empty) foundUser = snapshot.docs[0].data();
    }
    if (foundUser) {
      setSelectedUser(foundUser);
      setCurrentScreen("chat");
    } else alert("Usuário não encontrado");
  };

  return (
    <div className={`${styles.container} ${theme==="dark"?styles.darkTheme:""}`} style={{ fontFamily: font }}>
      {/* Sidebar */}
      <div className={styles.sidebar}>
        <div>
          <div className={styles.userProfile}>
            <img src={user?.photoURL || "https://via.placeholder.com/50"} />
            <div>
              <strong>{user?.displayName}</strong>
              <p>{user?.nametag || "Nametag"}</p>
            </div>
          </div>

          <ul className={styles.userList}>
            {users.map(u => (
              <li key={u.uid} className={selectedUser?.uid === u.uid ? "selected" : ""} onClick={()=>{setSelectedUser(u); setCurrentScreen("chat")}}>
                {u.name} ({u.nametag})
              </li>
            ))}
          </ul>
        </div>

        <div>
          <button onClick={handleLogout}>Sair da conta</button>
          <button onClick={()=>setCurrentScreen("config")}>Configurações</button>
          <button onClick={()=>setCurrentScreen("profile")}>Perfil</button>
          <button onClick={()=>setCurrentScreen("voice")}>Chat de voz</button>
          <button onClick={()=>setCurrentScreen("addContact")}>Adicionar contato</button>
        </div>
      </div>

      {/* Área principal */}
      <div className={styles.main}>
        {currentScreen==="chat" && selectedUser && (
          <>
            <div className={styles.messages}>
              {messages.map((msg, idx)=>(
                <div key={idx} className={`${styles.message} ${msg.senderId===user.uid?styles.sent:styles.received}`}>
                  {msg.text}
                </div>
              ))}
              <div ref={messagesEndRef}></div>
            </div>
            <div className={styles.inputContainer}>
              <input value={newMessage} onChange={e=>setNewMessage(e.target.value)} onKeyDown={e=>e.key==="Enter" && handleSendMessage()} placeholder="Digite sua mensagem..." />
              <button onClick={handleSendMessage}>Enviar</button>
            </div>
          </>
        )}

        {currentScreen==="config" && (
          <div className={styles.configScreen}>
            <h2>Configurações</h2>
            <button onClick={handleThemeChange}>Tema: {theme}</button>
            <div>
              <label>Fonte: </label>
              <select value={font} onChange={handleFontChange}>
                <option>Arial</option>
                <option>Verdana</option>
                <option>Tahoma</option>
                <option>Courier New</option>
              </select>
            </div>
          </div>
        )}

        {currentScreen==="profile" && (
          <ProfileScreen user={user} handleProfileUpdate={handleProfileUpdate} />
        )}

        {currentScreen==="addContact" && (
          <div className={styles.addContactScreen}>
            <h2>Adicionar Contato</h2>
            <input placeholder="Email" value={contactEmail} onChange={e=>setContactEmail(e.target.value)} />
            <input placeholder="Nametag" value={nametagInput} onChange={e=>setNametagInput(e.target.value)} />
            <button onClick={handleAddContact}>Adicionar</button>
          </div>
        )}

        {currentScreen==="voice" && (
          <div className={styles.voiceScreen}>
            <h2>Chat de Voz (WebRTC)</h2>
            <p>A implementação básica de WebRTC pode ser adicionada aqui.</p>
          </div>
        )}
      </div>
    </div>
  );
}

// Componente separado de Perfil
function ProfileScreen({ user, handleProfileUpdate }) {
  const [newNametag, setNewNametag] = useState(user?.nametag || "");
  const [newPhoto, setNewPhoto] = useState(null);

  return (
    <div className={styles.profileScreen}>
      <h2>Perfil</h2>
      <img src={user?.photoURL || "https://via.placeholder.com/80"} style={{ borderRadius:"50%" }} />
      <p>{user?.displayName}</p>
      <p>Nametag: {user?.nametag}</p>

      <input placeholder="Nova Nametag" value={newNametag} onChange={e=>setNewNametag(e.target.value)} />
      <input type="file" onChange={e=>setNewPhoto(e.target.files[0])} />
      <button onClick={()=>handleProfileUpdate(newNametag, newPhoto)}>Atualizar Perfil</button>
    </div>
  );
}
